*{
  margin: 0%;
  padding: 0%;
  font-family: 'Roboto', sans-serif;
}
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700;900&display=swap');
 

nav{
  background-color: rgb(44,30,242);
  color: white;
  padding:10px;
  text-align: center;
}

.container{
  background-color: rgb(255,233,209);
  width: 70%;
  margin: auto;
  min-height: 100vh;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
}

.container >form{
  display:flex;
  flex-direction:column;
}

.container>h1{
  text-align:center;
  margin:30px;
  font-size: 2.5rem;
}
.container >form >input,textarea,button{
  padding:15px;
  border:1px solid rgba(0,0,0,0.214);
  border-radius: 5px;
  margin:10px 0px;
  font-size: 1.2rem;
}
button{
  border:none;
  cursor:pointer;
  background-color: rgb(37,37,37) ;
  color: white;
  
}



.task{
  display:flex;
  align-items: center ;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  margin: 20px 0px;
}

.task >div{
  width: 100%;
}
.task span{
  color: rgba(0, 0, 0, 0.673);
}
.task button{
  border-radius: 100%;
  height: 30px;
  width: 30px;
  font-size: 1.6rem;
  background-color: crimson;
  display:flex;
  justify-content: center;
  align-items: center;
}

.task button:hover{
  background-color: red;
}


